.App {
  text-align: center; }

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px; }

.App-header {
  background-color: #222;
  height: 100px;
  padding: 20px;
  color: white; }

.App-title {
  font-size: 3em;
  text-align: center; }

.App-intro {
  font-size: large; }

.App-columns {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center; }

.App-column-pane {
  flex: 1; }

.App-submit-button {
  width: 160px;
  padding: 20px;
  margin: 20px;
  font-size: 1.5em; }

.App-fullscreen {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center; }

.App-start-button {
  background-color: lightgreen;
  width: 50%;
  height: 50%;
  font-size: 3em; }

.App-rows {
  display: flex;
  flex-direction: column; }

.App-gameover {
  padding: 30px;
  font-size: 5em !important;
  background-color: red !important; }

.App-flash {
  font-size: 4em; }

.App-flash-2x {
  font-size: 8em; }

.App-flash-3x {
  font-size: 12em; }

.App-chessbox {
  flex: 1;
  margin: 30px;
  width: 100%;
  border: 3px black dashed; }

.App-chessbox > .App-columns {
  width: 100%;
  height: 100%; }

.App-turn-col {
  flex: 1;
  height: inherit;
  display: flex;
  justify-content: center;
  align-items: center; }

.App-turn-button {
  height: 100%;
  flex: 1;
  background-color: orange;
  font-size: 2.5em; }

.App-workoutbox {
  flex: 1;
  width: 100%;
  background-color: yellowgreen;
  justify-content: space-evenly;
  align-items: center;
  position: relative; }

.chess-pattern {
  background: #eee;
  background: linear-gradient(135deg, transparent 75%, rgba(255, 255, 255, 0.1) 0%) 0 0, linear-gradient(-45deg, transparent 75%, rgba(255, 255, 255, 0.1) 0%) 60px 60px, linear-gradient(135deg, transparent 75%, rgba(255, 255, 255, 0.1) 0%) 60px 60px, linear-gradient(-45deg, transparent 75%, rgba(255, 255, 255, 0.1) 0%) 0 0, #253137;
  background-size: 120px 120px;
  border: none;
  color: white;
  margin: 10px; }

.blink {
  animation: blinker 1s linear infinite; }

.row-reverse {
  flex-direction: row-reverse; }

@keyframes spin {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }

@keyframes blinker {
  50% {
    opacity: 0; } }

.App-workoutbox:before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 2;
  background-image: url("data:image/png;base64, "); }

.App-workoutbox:after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
  background: repeating-linear-gradient(to right, rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1) 70px, transparent 70px, transparent 140px); }

